














































import Vue from "vue"

const routes = { "lotty": "indigo", "gym": "deep-purple", "home": "cyan"}

export default Vue.extend({
  name: "Navbar",
  data: () => ({
    items: [{
      title: "Home",
      icon: "mdi-home",
      color: "indigo",
      route: "/workouts/home"
    }, {
      title: "Gym",
      icon: "mdi-weight-lifter",
      color: "green",
      route: "/workouts/gym"
    },
    {
      title: "Lotty",
      icon: "mdi-face-woman-shimmer-outline",
      color: "blue",
      route: "/workouts/lotty"
    }]
  }),
  computed: {
    color() {
      const found = this.$route.path.split("/workouts/");
      // @ts-ignore
      return routes[found[1]];
    }
  }
})
